.banner {
  color: white;
  object-fit: contain;
  height: 448px;
  position: relative;
}
.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
@media (max-width: 400px) {
  .banner__description {
    max-width: 200px;
  }
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  font-weight: 700;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-radius: 0.2vw;
  background-color: rgba(51, 51, 51, 0.5);
  border: none;
  transition: all 0.2s;
}
.banner__button:hover {
  background-color: #e6e6e6;
  color: #000;
}
.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  position: absolute;
  bottom: 0;
  width: 100%;
}
